import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { PagesComponent } from './pages.component';
import { Routes, RouterModule } from '@angular/router';
import { NbLayoutModule, NbSidebarModule, NbBidiModule, NbMenuModule, NbIconModule, NbCardModule, } from '@nebular/theme';

const routes: Routes = [
  {
    path: '', component: PagesComponent, children: [
      { path: 'dashboard', loadChildren: () => import('./dashboard/dashboard.module').then(m => m.DashboardModule) },
      { path: 'productos', loadChildren: () => import('./productos/productos.module').then(m => m.ProductosModule) },
      { path: 'agentes', loadChildren: () => import('./agentes/agentes.module').then(m => m.AgentesModule) },
      { path: 'clientes', loadChildren: () => import('./clientes/clientes.module').then(m => m.ClientesModule) },
      { path: 'compras', loadChildren:()=> import('./compras/compras.module').then(m=>m.ComprasModule)},
      { path: '', redirectTo: 'dashboard', pathMatch: 'full' }
    ]
  }

]


@NgModule({
  declarations: [PagesComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NbLayoutModule,
    NbSidebarModule,
    NbBidiModule,
    NbMenuModule,
    NbIconModule,
    NbCardModule
  ],
  exports: [RouterModule],
})
export class PagesModule { }
